<script>
  import { onMount } from 'svelte';

  let socket;
  let messages = [];
  let input = '';
  let status = ' Connecting...';
  let users = [];
  let selected = 'all'; // mc nh chat vi All
  let myName = '';

  const SERVER_URL = import.meta.env.DEV ? 'ws://localhost:3000' : 'wss://render-server-ezuf.onrender.com';

  function connect() {
    socket = new WebSocket(SERVER_URL);

    socket.onopen = () => {
      status = ' Connected';
    };

    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);

      if (data.type === 'welcome') {
        myName = data.name;
      } else if (data.type === 'users') {
        users = [{ id: 'all', name: 'All' }, ...data.users];
      } else if (data.type === 'chat') {
        messages = [...messages, data];
      }
    };

    socket.onclose = () => {
      status = ' Disconnected  retrying...';
      setTimeout(connect, 2000);
    };
  }

  function sendMessage() {
    if (input.trim() && socket.readyState === WebSocket.OPEN) {
      socket.send(
        JSON.stringify({
          type: 'chat',
          to: selected,
          message: input
        })
      );
      input = '';
    }
  }

  onMount(() => {
    //connect();
  });
</script>

<main class="flex h-screen bg-gray-100 text-gray-800">
  <!-- Sidebar user list -->
  <aside class="w-48 bg-white border-r p-3">
    <h2 class="font-bold mb-2"> Users </h2>
    {#each users as u}
      <div
        on:click={() => (selected = u.id)}
        class="p-2 rounded cursor-pointer mb-1"
        class:bg-blue-100={selected === u.id}>
        {u.name}
      </div>
    {/each}
  </aside>

  <!-- Chat area -->
  <div class="flex-1 flex flex-col">
    <div class="flex-1 border-b p-3 overflow-y-auto">
      {#each messages as msg}
        <div class="mb-1">
          <span class="font-bold">{msg.from}</span>
          <span class="text-gray-500"> {msg.to === 'all' ? 'All' : 'User ' + msg.to}:</span>
          <span class="ml-2">{msg.message}</span>
        </div>
      {/each}
    </div>

    <form on:submit|preventDefault={sendMessage} class="flex p-3 gap-2">
      <input
        bind:value={input}
        placeholder="Type message..."
        class="flex-grow border rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400" />
      <button type="submit" class="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition">
        Send
      </button>
    </form>
  </div>
</main>
