<script>
  import { afterUpdate, onMount } from 'svelte';
  import { status, users, messages, hasMore, loadMessages, sendMessage, myName, myId } from '$lib/websocket-client.js';

  let input = '';
  let selected = 'all';
  let chatBox;

  function handleSend() {
    if (input.trim()) {
      sendMessage({ type: 'chat', to: selected, message: input });
      input = '';
    }
  }

  onMount(async () => {
    await loadMessages(); // load 50 tin mi nht
    chatBox.scrollTop = chatBox.scrollHeight; // cun xung cui
  });

  async function handleScroll() {
    if (chatBox.scrollTop === 0 && $hasMore) {
      const oldest = $messages[0];
      await loadMessages(oldest.created_at); // load thm c
      chatBox.scrollTop = 10; // gi v tr
    }
  }

  afterUpdate(() => {
    if (chatBox) {
      chatBox.scrollTop = chatBox.scrollHeight;
    }
  });
</script>

<main class="flex flex-col items-center justify-center h-screen bg-gray-100 text-gray-800">
  <div class="w-full max-w-md bg-white shadow-xl rounded-2xl p-6">
    <h1 class="text-2xl font-bold mb-2 text-center">
       WebSocket Chat Demo [{$users?.length || 0}]
    </h1>
    <p class="text-sm text-center mb-4">{$status}</p>

    <!-- Chat Box -->
    <div
      bind:this={chatBox}
      on:scroll={handleScroll}
      class="border rounded-lg h-64 overflow-y-auto p-3 bg-gray-50 mb-3 flex flex-col gap-2">
      {#each $messages as msg}
        {#if msg.type === 'welcome'}
          <div class="text-center text-gray-500 italic text-sm">
            {msg.message}, {msg.name} ({msg.id})
          </div>
        {:else if msg.type === 'chat'}
          <div class="flex {msg.from === $myName ? 'justify-end' : 'justify-start'}">
            <div
              class="max-w-[75%] p-2 rounded-lg text-sm"
              class:bg-blue-200={msg.from === $myName}
              class:bg-green-100={msg.from !== $myName}>
              {#if msg.from !== $myName}
                <b>{msg.from}</b>
                
                <i>{msg.to === 'all' ? 'All' : 'User ' + msg.to}</i>
                :
              {/if}
              {msg.message}
            </div>
          </div>
        {/if}
      {/each}
    </div>

    <!-- Input -->
    <form on:submit|preventDefault={handleSend} class="flex gap-2">
      <input
        bind:value={input}
        placeholder="Type message..."
        class="flex-grow border rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400" />
      <button type="submit" class="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition">
        Send
      </button>
    </form>
  </div>
</main>
