<script>
  import { onMount } from 'svelte';
  import { user, accessToken, loading } from '$lib/stores';
  import { goto } from '$lib/router.js';

  import Router from '@routes/Router.svelte';
  import Home from '@routes/Home.svelte';
  import Login from '@routes/Login.svelte';
  import AuthSuccess from '@routes/AuthSuccess.svelte';

  import ApiClient from '$lib/api';
  const url = import.meta.env.URL || 'https://render-server-ezuf.onrender.com';
  const api = new ApiClient();
  api.baseURL = url;

  //  Danh sch route
  const routes = {
    '/': Home,
    '/login': Login,
    '/auth/success': AuthSuccess,
    '*': Login
  };

  //  Kim tra login bng refreshToken (cookie)
  onMount(async () => {
    loading.set(true);
    //console.clear();
    try {
      const _url = url + '/auth/me';
      const res = await fetch(_url, {
        credentials: 'include'
      });
      const data = await res.json();
      // const data = await api.get('/auth/me');

      console.log('_url', _url);
      console.log('AUTH ME RESPONSE:', data);
      if (data.user) {
        user.set(data.user);
        accessToken.set(data.accessToken);
      } else {
        user.set(null);
        accessToken.set(null);
      }
    } catch (e) {
      console.error('AUTH ERROR:', e);
      user.set(null);
      accessToken.set(null);
    }

    loading.set(false);
  });

  //  Route Guard CHUN
