  //     goto('/login');
  //   }

  //   //  2. Nu  login  khng cho vo /login
  //   if ($user && path === '/login') {
  //     goto('/');
  //   }
  // }

  //$: console.log('Appinfo:', $user, $accessToken, $loading);
</script>

