<script>
  import { onMount, onDestroy } from 'svelte';
  import { fromEvent, Subscription } from 'rxjs';
  import { debounceTime, map, distinctUntilChanged, filter } from 'rxjs/operators';
  import { fade, scale } from 'svelte/transition';
  import { fadeScale } from './transitions.js';

  let isOpen = false;
  let inputRef;
  let results = [];
  let subscription;

  // Mock danh sch d liu (gi lp server-side)
  const allItems = ['Home', 'Docs', 'Blog', 'Pricing', 'Contact', 'Components', 'Dashboard', 'Settings'];

  function openSpotlight() {
    isOpen = true;
    results = [];
    setTimeout(() => inputRef?.focus(), 0);
  }

  function closeSpotlight() {
    isOpen = false;
    subscription?.unsubscribe(); // cleanup
  }

  async function fetchSearch(query) {
    console.log(' Gi server vi query:', query);

    try {
      const res = await fetch('https://jsonplaceholder.typicode.com/posts');
      const data = await res.json();

      console.log(' Kt qu t server:', data);

      results = data.filter((item) => item.title.toLowerCase().includes(query.toLowerCase())).slice(0, 10); // ch ly 10 kt qu u
    } catch (err) {
      console.error(' Li gi API:', err);
      results = [];
    }
  }

  onMount(() => {
    // Hotkey Cmd+K or Ctrl+K
    const keyHandler = (e) => {
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault();
        openSpotlight();
      }
      if (e.key === 'Escape') {
        closeSpotlight();
      }
    };
    window.addEventListener('keydown', keyHandler);

    // RxJS input search logic
    const subInput = () => {
      if (!inputRef) return;
      subscription = fromEvent(inputRef, 'input')
        .pipe(
          map((e) => e.target.value.trim()),
          filter((text) => text.length > 0),
          debounceTime(500),
          distinctUntilChanged()
        )
        .subscribe((query) => {
          fetchSearch(query);
        });
    };

    const checkOpenInterval = setInterval(() => {
      if (isOpen && inputRef) {
        subInput();
        clearInterval(checkOpenInterval);
      }
    }, 100);

    return () => {
      window.removeEventListener('keydown', keyHandler);
      subscription?.unsubscribe();
    };
  });
</script>

{#if isOpen}
  <div class="fixed inset-0 bg-black/40 z-40" on:click={closeSpotlight}></div>
  <div
    class="fixed top-[20%] left-1/2 -translate-x-1/2 z-50 bg-white dark:bg-zinc-900 w-full max-w-md rounded-xl shadow-xl p-4"
    transition:fadeScale={{ duration: 300 }}
    on:click|stopPropagation={() => {}}>
    <input
      bind:this={inputRef}
      type="text"
      placeholder="Search..."
      class="w-full p-3 border border-gray-300 rounded focus:outline-none focus:ring" />

    {#if results.length > 0}
      <ul class="mt-4 max-h-60 overflow-y-auto divide-y divide-gray-100 dark:divide-gray-700">
        {#each results as item}
          <li class="px-4 py-2 hover:bg-zinc-100 dark:hover:bg-zinc-800 cursor-pointer">
            {item.title}
          </li>
        {/each}
      </ul>
    {:else}
      <div class="mt-4 text-center text-gray-400">Khng c kt qu.</div>
    {/if}
  </div>
{/if}
