// websocket-client.js
import { writable } from 'svelte/store';

const WEBSOCKET_URL = import.meta.env.DEV
    ? 'ws://localhost:3000'
    : 'wss://render-server-ezuf.onrender.com';
const SERVER_URL = import.meta.env.DEV
    ? 'http://localhost:3000'
    : 'https://render-server-ezuf.onrender.com';

// Store chung
export const status = writable(' Connecting...');
export const users = writable([]);
export const messages = writable([]);   // cha c history + realtime
export const hasMore = writable(true);
export const myId = writable(localStorage.getItem('myId') || null);
export const myName = writable(localStorage.getItem('myName') || null);

let socket;

// Hm load t API (history)
export async function loadMessages(before = null) {
    const url = new URL('/messages', SERVER_URL);
    if (before) url.searchParams.set('before', before);
    url.searchParams.set('limit', 50);

    const myId = localStorage.getItem('myId');
    if (myId) url.searchParams.set('userId', myId);

    console.log('load message with id:', myId, url.toString());

    const res = await fetch(url.toString());
    if (!res.ok) throw new Error(`HTTP error! Status: ${res.status}`);

    const data = await res.json();

    if (data.length === 0) {
        hasMore.set(false);
        return;
    }

    messages.update(m => [...data, ...m]);
}

// Hm connect WebSocket (realtime)
function connect() {
    socket = new WebSocket(WEBSOCKET_URL);

    socket.onopen = () => {
        status.set(' Connected');
    };

    socket.onmessage = (event) => {
        try {
            const data = JSON.parse(event.data);

            if (data.type === 'welcome') {
                myId.set(data.id);
                myName.set(data.name);

                // lu vo localStorage
                localStorage.setItem('myId', data.id);
                localStorage.setItem('myName', data.name);

                messages.update(m => [...m, data]);
            } else {
                messages.update(m => [...m, data]);
            }
        } catch {
            messages.update(m => [...m, { type: 'system', text: event.data }]);
        }
    };

    socket.onclose = () => {
        status.set(' Disconnected  retrying...');
        setTimeout(connect, 2000);
    };
}

connect();

// Hm gi message
export function sendMessage(payload) {
    if (socket && socket.readyState === WebSocket.OPEN) {
        socket.send(JSON.stringify(payload));
    }
}
